/**
 * @param {import('kaboom').KaboomCtx} k
 */
export default function eventsPlugin (k) {
  function touchStart (callback) {
    let skip = true
    let done = false

    return {
      add () {
        k.mouseDown(() => {
          if (skip || done) {
            skip = false
            return
          }

          if (this.hasPt(k.mousePos())) {
            callback.call(this)
          }

          done = true
        })

        k.mouseRelease(() => {
          skip = true
          done = false
        })
      }
    }
  }

  return { touchStart }
}
